import Vue from 'vue'
import Router from 'vue-router'
import jdd from '@/components/jdd/jdd.vue'
//import Hello from '@/components/Hello'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'jsondiff',
      component: jdd
    }
  ]
})



