
<template>

<div id="main">
    <div class="header">
	<h1>JSON Diff</h1>
	<h3>The semantic JSON compare tool</h3>

	<div class="weak">
	    <p>
		Validate, format, and compare two JSON documents.  See the differences between the objects instead of just the new lines and mixed up properties.
	    </p>

	    <p>
		Created by <a href="http://www.zackgrossbart.com">Zack Grossbart</a>.  Get the <a href="https://github.com/zgrossbart/jdd">source code</a>.
	    </p>

	    <p>
		Big thanks owed to the team behind <a href="http://www.jsonlint.com">JSONLint</a>.
	    </p>

	</div>
    </div>

    <div class="initContainer">
	<div class="left">
	    <textarea spellcheck="false" id="textarealeft" placeholder="Enter JSON to compare, enter an URL to JSON"></textarea>
	    <pre id="errorLeft" class="error"></pre>
	    <span class="fileInput">or <input type="file" id="fileLeft" v-on:change="handleFiles($event.target.files, 'left')"></span>
	</div>

	<div class="center">
	    <button id="compare" v-on:click="compare">Compare</button>
	    <div class="throbber-loader"></div>
	    <br/><br/><br/><br/>
	    or try some <a href="#" id="sample" v-on:click="loadSampleData">sample data</a>

	</div>

	<div class="right">
	    <textarea spellcheck="false" class="right" id="textarearight" placeholder="Enter JSON to compare, enter an URL to JSON"></textarea>
	    <pre id="errorRight" class="error"></pre>
	    <span class="fileInput">or <input type="file" id="fileRight" v-on:change="handleFiles($event.target.files, 'right')"></span>
	</div>
    </div>

    <div class="diffcontainer">
	<div id="report">
	</div>
	<pre id="out" class="left codeBlock"></pre>
	<pre id="out2" class="right codeBlock"></pre>
	<ul id="toolbar" class="toolbar"></ul>
    </div>
</div>
</template>


<script>
import {jdd} from "./js/jdd"
import "./css/reset.css"
import "./css/throbber.css"
import "./css/jdd.css"
export default {
    name: 'diffview',
    methods: {
        compare: jdd.compare,
		handleFiles: jdd.handleFiles,
		loadSampleData: jdd.loadSampleData,
    }
}
</script>



